#loadingSpan {
  color: ghostwhite;
  display: flex;
  font-size: 1.3em;
  font-style: italic;
  justify-content: center;
  margin: 1% 40%;
  width: 20%;
}

/* REF: https://dev.to/debadeepsen/a-table-with-a-fixed-sticky-header-54gm */
#SWPlanetsTableContainer {
  border-bottom: dashed 1px #e4b732;
  height: 750px;
  margin-top: 1%;
  overflow-y: auto;
  width: 100%;
}

#SWPlanetsTableContainer::-webkit-scrollbar {
  width: 8px;
}

#SWPlanetsTableContainer::-webkit-scrollbar-track {
  background: ghostwhite;
}

#SWPlanetsTableContainer::-webkit-scrollbar-thumb {
  background: #e4b732;
}

#SWPlanetsTableContainer::-webkit-scrollbar-thumb:hover {
  background: rgba(172, 164, 164, 0.87);
}

#SWPlanetsTable {
  border-collapse: collapse;
  width: 100%;
}

#SWPlanetsTable th {
  background-color: #e4b732;
  color: black;
  font-size: 1.3em;
  font-weight: 600;
  padding: 5px;
  position: sticky;
  text-align: center;
  top: 0;
}

#SWPlanetsTable tbody {
  color: ghostwhite;
  font-size: 1.2em;
  text-align: center;
}

#SWPlanetsTable td {
  border: dashed 1px #d5a40c;
  padding: 5px;
}

.filmsContainer {
  list-style: none;
  margin: 5px 8px;
  padding: 0;
}
