@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;700&display=swap);
#pageHeader {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 1%;
  padding: 1.5%;
}

#headerCentralContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 40%;
}

#headerCentralContainer img {
  width: 250px;
}

#headerCentralContainer input[ type = 'text' ] {
  -webkit-align-self: center;
          align-self: center;
  background-color: #fce08d;
  border-radius: 4px;
  font-size: 1.15em;
  padding: 1%;
  transition: 0.3s width ease-in-out;
  width: 33%;
}

#headerCentralContainer input[ type = 'text' ]:focus {
  background-color: #d4f1f4;
  outline: none;
  width: 40%;
}

#loadingSpan {
  color: ghostwhite;
  display: -webkit-flex;
  display: flex;
  font-size: 1.3em;
  font-style: italic;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 1% 40%;
  width: 20%;
}

/* REF: https://dev.to/debadeepsen/a-table-with-a-fixed-sticky-header-54gm */
#SWPlanetsTableContainer {
  border-bottom: dashed 1px #e4b732;
  height: 750px;
  margin-top: 1%;
  overflow-y: auto;
  width: 100%;
}

#SWPlanetsTableContainer::-webkit-scrollbar {
  width: 8px;
}

#SWPlanetsTableContainer::-webkit-scrollbar-track {
  background: ghostwhite;
}

#SWPlanetsTableContainer::-webkit-scrollbar-thumb {
  background: #e4b732;
}

#SWPlanetsTableContainer::-webkit-scrollbar-thumb:hover {
  background: rgba(172, 164, 164, 0.87);
}

#SWPlanetsTable {
  border-collapse: collapse;
  width: 100%;
}

#SWPlanetsTable th {
  background-color: #e4b732;
  color: black;
  font-size: 1.3em;
  font-weight: 600;
  padding: 5px;
  position: -webkit-sticky;
  position: sticky;
  text-align: center;
  top: 0;
}

#SWPlanetsTable tbody {
  color: ghostwhite;
  font-size: 1.2em;
  text-align: center;
}

#SWPlanetsTable td {
  border: dashed 1px #d5a40c;
  padding: 5px;
}

.filmsContainer {
  list-style: none;
  margin: 5px 8px;
  padding: 0;
}

#numericFilterContainer {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 1.5%;
}

#numericFilterForm {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-bottom: 0.3%;
  width: 60%;
}

#numericFilterForm select {
  background-color: #fce08d;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.15em;
  padding: 1%;
  width: 20%;
}

#numericFilterForm select:focus {
  background-color: #d4f1f4;
  outline: none;
}

#numericFilterForm input[ type = 'number' ] {
  -webkit-align-self: center;
          align-self: center;
  background-color: #fce08d;
  border-radius: 4px;
  font-size: 1.15em;
  padding: 1%;
  transition: 0.3s ease-in-out;
  width: 20%;
}

#numericFilterForm input[ type = 'number' ]:focus {
  background-color: #d4f1f4;
  outline: none;
}

#filterBtn {
  background-color: #d4f1f4;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.15em;
  transition: 0.3s width ease-in-out;
  width: 15%;
}

#filterBtn:hover {
  background-color: #d4f1f4;
  width: 18%;
}

#filterBtn:disabled {
  background-color: rgba(170, 170, 170, 0.466);
  border: none;
  border-radius: 5px;
  color: ghostwhite;
  font-size: 1.15em;
  transition: 0.3s width ease-in-out;
  width: 15%;
}

#filterBtn:disabled:hover {
  background-color: rgba(170, 170, 170, 0.466);
  box-shadow: none;
  color: ghostwhite;
  cursor: default;
  width: 15%;
}

#eachChosenFilter {
  background-color: #d4f1f4;
  border-radius: 5px;
  color: black;
  display: -webkit-flex;
  display: flex;
  font-size: 1.15em;
  font-style: italic;
  margin-top: 0.8%;
  padding: 6px;
  text-align: center;
}

#eachChosenFilter button {
  background-color: #d4f1f4;
  border: none;
  color: red;
  cursor: pointer;
  font-size: 0.9em;
  margin-left: 5px;
}

footer {
  background-color: #e4b732;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 1.5%;
  padding: 1%;
}

footer span {
  font-size: 1.5em;
  text-align: center;
}

footer span a {
  color: ghostwhite;
  font-weight: 700;
  text-decoration: none;
  transition-duration: 0.3s;
}

footer span a:hover {
  color: black;
  font-size: 1.05em;
}

* {
  box-sizing: border-box;
  font-family: Nunito , sans-serif;
  font-size: 1em;
}

body {
  background-image: url(/trybe_frontend_8-starwars_planets_search/static/media/space.ccbca696.jpg);
  color: ghostwhite;
}

